import React, { Component } from "react"
import { Link, animateScroll as scroll } from "react-scroll"

class NavVert extends React.Component {
    
  render() {
    return (
      <nav className="nav-container  is-hidden-mobile" id="indicator">
        <ul className="nav">
          <li className="page-scroll">
            <Link
             
              to="introduction"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
            >
              introduction
            </Link>
          </li>
          <li className="page-scroll">
          <Link
           
              to="skillset"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
            >
              skills
            </Link>
          </li>
          <li className="page-scroll">
          <Link
           
              to="experience"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
            >
              experience
            </Link>
         
          </li>
          <li className="page-scroll">
          <Link
        
              to="portfolio"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
            >
              portfolio
            </Link>
          
          </li>
          <li className="page-scroll">
          <Link
            
              to="references"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
            >
              references
            </Link>
          
          </li>
          <li className="page-scroll">
          <Link
            
              to="connect"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
            >
              connect
            </Link>
          
          </li>
        </ul>
      </nav>
    )
  }
}

export default NavVert
