import React, { Component } from "react";


class Contact extends React.Component {
    
    render() {
      return ( 
        <section
        id="connect"     
        className="connect"
      
      >
        <div className="column is-10 is-offset-1">
          <h2 className="title">Kontakt</h2>
          <ul className="social-links social-border">
            <li className="google-plus">
              <i className="fab fa-google-plus-g" aria-hidden="true"></i>
            </li>
            <li className="facebook">
              <i className="fab fa-facebook-f" aria-hidden="true"></i>
            </li>
            <li className="dribbble">
              <i className="fab fa-dribbble" aria-hidden="true"></i>
            </li>
            <li className="behance">
              <i className="fab fa-behance" aria-hidden="true"></i>
            </li>
            <li className="linkdin">
              <i className="fab fa-linkedin" aria-hidden="true"></i>
            </li>
            <li className="twitter">
              <i className="fab fa-twitter" aria-hidden="true"></i>
            </li>
            <li className="instagram">
              <i className="fab fa-instagram" aria-hidden="true"></i>
            </li>
            <li className="vimeo">
              <i className="fab fa-vimeo" aria-hidden="true"></i>
            </li>
          </ul>
          <hr />
          <a className="button" href="mailto:hello@carobehler.de">E-Mail schreiben</a>
        </div>
         
      
      </section>
        )
    }
};
  
  export default Contact